.Spinner {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 42px;
  vertical-align: middle;
}

.Spinner__container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
  -webkit-animation: Spinner__rotateForever 2s linear infinite;
          animation: Spinner__rotateForever 2s linear infinite;
}

.Spinner__path {
  -webkit-animation: Spinner__dash 1.5s ease-in-out infinite;
          animation: Spinner__dash 1.5s ease-in-out infinite;
  stroke: var(--color-spinner);
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  stroke-linecap: round;
}

@-webkit-keyframes Spinner__rotateForever {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Spinner__rotateForever {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes Spinner__dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 99, 200;
    stroke-dashoffset: -20px;
  }

  100% {
    stroke-dasharray: 99, 200;
    stroke-dashoffset: -125px;
  }
}

@keyframes Spinner__dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 99, 200;
    stroke-dashoffset: -20px;
  }

  100% {
    stroke-dasharray: 99, 200;
    stroke-dashoffset: -125px;
  }
}



