.Input {
  margin: 8px;
  padding: 2px 10px 5px;
  width: 150px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

.Input:focus {
  outline: none;
  -webkit-box-shadow: inset 0 0 5px var(--color-input-shadow);
          box-shadow: inset 0 0 5px var(--color-input-shadow);
}



